import styled, { createGlobalStyle } from 'styled-components';

export const GlobalStyle = createGlobalStyle`
* {
    box-sizing: border-box;
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    font-size: 24px;
    line-height: 1.5;
    background-color: floralwhite;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

nav {
    display: flex;
    flex-direction: column;
}

ul {
    list-style: none;
    padding: 0;
}

h1, h2, h3 {
    margin: 0;
}

button {
    text-decoration: none;
    color: blue;
    cursor: pointer;
}

#main {
    display: flex;
    flex-direction: column; /*      */
}

#menu {
    margin-right: 50px;
    padding-top: 50px;
    padding-right: 50px;
    border-right: 2px solid lightgray;
    font-weight: bold;
}
    `;

export const CardContainer = styled.div`
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 20px;
    width: 100%;
    padding: 0 100px;

    @media (max-width: 1200px) {
        grid-template-columns: repeat(4, 1fr);
        padding: 0 80px;
    }

    @media (max-width: 992px) {
        grid-template-columns: repeat(3, 1fr); //      
        padding: 0 70px;
    }

    @media (max-width: 768px) {
        grid-template-columns: repeat(2, 1fr);
        padding: 0 60px;
    }

    @media (max-width: 576px) {
        grid-template-columns: 1fr;
        padding: 0 50px;
    }
`;

export const TitleCatalog = styled.h1`
    text-align: center;
`;

export const Card = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-direction: column;
    border: 1px solid #ccc;
    border-radius: 8px;
    padding: 16px;
    background: white;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
    min-width: 185px;
    max-width: 285px;
    flex: 1;

    @media (max-width: 1200px) {
        padding: 14px;
    }

    @media (max-width: 992px) {
        padding: 12px;
    }

    @media (max-width: 768px) {
        padding: 10px;
    }

    @media (max-width: 576px) {
        padding: 8px;
    }
`;

export const Image = styled.img`
    width: 150px;
    height: 200px;
    margin-bottom: 16px;
    object-fit: contain;
    display: block;

    @media (max-width: 1200px) {
        width: 140px;
        height: 190px;
    }

    @media (max-width: 992px) {
        width: 130px;
        height: 180px;
    }

    @media (max-width: 768px) {
        width: 120px;
        height: 170px;
    }

    @media (max-width: 576px) {
        width: 110px;
        height: 160px;
    }
`;

export const Title = styled.h2`
    font-size: 19px;
    margin: 0;

    @media (max-width: 1200px) {
        font-size: 18px;
    }

    @media (max-width: 992px) {
        font-size: 17px;
    }

    @media (max-width: 768px) {
        font-size: 16px;
    }

    @media (max-width: 576px) {
        font-size: 15px;
    }
`;

export const description = styled.p`
    flex-grow: 1;
    font-size: 10px;
    overflow: hidden;
    margin: 4px 0;
    padding-right: 10px;

    @media (max-width: 1200px) {
        font-size: 19px;
    }

    @media (max-width: 992px) {
        font-size: 18px;
    }

    @media (max-width: 768px) {
        font-size: 17px;
    }

    @media (max-width: 576px) {
        font-size: 16px;
    }

    &::-webkit-scrollbar {
        display: none;
    }

    scrollbar-width: none;
    -ms-overflow-style: none;
`;

export const Price = styled.p`
    color: green;
    margin-left: auto;
`;

export const Button = styled.button`
    background-color: rgb(255 144 0 / 67%);
    color: black;
    border: none;
    border-radius: 4px;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    transition:
        all 0.3s ease,
        transform 0.2s ease;

    &:hover {
        background-color: rgb(255 144 0);
        color: white;
    }

    &:active {
        transform: scale(0.95);
    }
`;

export const ModalField = styled.div`
    position: fixed; 
    bottom: 30px; 
    right: 30px; 
    background-color: rgb(178 208 220);
    border-radius: 8px; 
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); 
    z-index: 1000; 
`;
